:root {
  --color-1: #7e57c2; /* Purple */
  --color-2: #1976d2; /* Blue */
  --color-3: #ff6f00; /* Orange */
  --color-4: #8bc34a; /* Green */
  --color-5: #f44336; /* Red */
  --color-6: #9c27b0; /* Deep Purple */
  --color-7: #ff9800; /* Amber */
  --color-8: #4caf50; /* Light Green */
  --color-9: #e91e63; /* Pink */
}

.drum-pad.pad-81 {
  background-color: var(--color-1);
}

.drum-pad.pad-87 {
  background-color: var(--color-2);
}

.drum-pad.pad-69 {
  background-color: var(--color-3);
}

.drum-pad.pad-65 {
  background-color: var(--color-4);
}

.drum-pad.pad-83 {
  background-color: var(--color-5);
}

.drum-pad.pad-68 {
  background-color: var(--color-6);
}

.drum-pad.pad-90 {
  background-color: var(--color-7);
}

.drum-pad.pad-88 {
  background-color: var(--color-8);
}

.drum-pad.pad-67 {
  background-color: var(--color-9);
}


*{
  margin: 0;
}

body {
  font-size: 24px;
}

h2 {
  font-size: 28px
}

#drum-machine {
  margin: 15px;
  height: 100vh;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
}

.wrapper {
    width: 100%;
    display: grid;
    padding: 10px;
    grid-gap: 15px;
    margin-top: 40px;
    border-radius: 15px;
    grid-template-columns: 1fr;
    background-color: darkgrey;
}

.keyboard {
    display: grid;
    grid-gap: 10px;
    grid-template-columns: repeat(3, 1fr);
}

.keyboard .drum-pad {
  width: 100%;
  height: 80px;
  outline: none;
  font-size: 24px;
  cursor: pointer;
  border-radius: 10px;
  background-color: #ffffff;
  transition: all 0.3s ease-in-out;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.3);
}

.keyboard .drum-pad.active {
  background-color: #ffcc00; /* Change the color to the desired active color */
  box-shadow: 0 0 10px rgba(255, 204, 0, 0.8); /* Add a glow effect */
}


@media (min-width: 768px) {
  .wrapper {
    width: 680px;
  }
}

@media (min-width: 992px) {
  .wrapper {
    grid-template-columns: 1fr 1fr;
  }
}

.controle {
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
}

.controle > button,
.controle > input,
.controle > h2 {
  margin-bottom: 10px;
}

.controle button {
  border: none;
  color: #ffffff;
  cursor: pointer;
  font-size: 20px;
  padding: 8px 10px;
  border-radius: 5px;
  background-color: #000000;
}
